<template>
  <div
    v-for="filter in filter"
    :key="filter"
    :class="`${filter} filter-item`"
    :style="`background-image:url(${이미지})`"
   @click="fire">
    {{ filter }}
  </div>
</template>

<script>
export default {
  data() {
    return {
      filter: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
    };
  },
  props: {
    이미지: String,
  },
  methods: {
    fire() {
      this.emitter.emit("BoxClick", this.filter);
    },
  },
};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: rgb(255, 255, 255);
  background-size: cover;
  background-position: center;
}
</style>