import axios from 'axios'
import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            likes: 36,
            like: false,
            more : {},
            name : 'dong'


        }
    },
    mutations: {
        //함수식으로 작성
        setMore(state, data){
            state.more = data
        },
        이름변경(state) {
            state.name = 'park'
        },
        좋아요(state) {
            if (state.like == false) {
                state.likes++
                state.like = true
            } else {
                state.likes--
                state.like = false
            }
        }
    },
    actions : {
        // ajax 요청하는 곳
        getData(context){
            axios.get(`https://codingapple1.github.io/vue/more0.json`).then((a)=>{
                console.log(a.data)
                context.commit('setMore', a.data)
            })
        }
    }
})
export default store