<template>
  <p>{{name}} {{내이름}} {{이름변경}}</p>
  <p>{{$store.state.more}}</p>
  <button @click="$store.dispatch('getData')"> 더보기 버튼 </button>
  <button @click="좋아요()"> 좋아요 </button>
  <button @click="$store.commit('싫어요')"> 싫어요 </button>
  <!-- <h4>안녕 {{ $store.state.name }}</h4>
  <button @click="$store.commit('이름변경')"></button> -->
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li @click="step = 2" v-if="step == 1">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container
    :step="step"
    :data="data"
    :이미지="이미지"
    @write="작성한글 = $event"
  />
  <button
    @click="
      more();
      moredata++;
    "
  >
    더보기
  </button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload($event)"
        multiple
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- <div v-if="step==0">내용0</div>
  <div v-if="step==1">내용1</div>
  <div v-if="step==2">내용2</div>
  <button @click="step=0">버튼0</button>
  <button @click="step=1">버튼1</button>
  <button @click="step=2">버튼2</button>
  <div style="margin-top: 500px"></div> -->
</template>

<script>
import Container from "./components/TheContainer.vue";
import data from "./components/PostData.js";
import axios from "axios";
import {mapMutations, mapState} from "vuex"
// axios.get('https://codingapple1.github.io/vue/more0.json').then()

export default {

  name: "App",
  data() {
    return {
      data: data,
      moredata: 0,
      step: 0,
      이미지: "",
      작성한글: "",
      필터 : "",
      카운터:0
    };
  },
  components: {
    Container,
  },
  computed:{
    ...mapState(['likes','name']),
    ...mapState({내이름:'name'})
  },
  methods: {
    ...mapMutations(['이름변경','좋아요']),
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.moredata}.json`)
        .then((result) => {
          result = result.data;
          this.data.push(result);
        });
    },
    upload(e) {
      let 파일 = e.target.files;
      let url = URL.createObjectURL(파일[0]);
      this.이미지 = url;
      this.step++;
      console.log(url);
    },
    publish(){
    var 내게시물 = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.이미지,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: "perpetua"
    };
    this.data.unshift(내게시물);
    this.step = 0;
}, 
  },
};
</script>

